import { defineComponent, h, ref, reactive } from 'vue'
import { useSuspense } from 'composable/components'
import { useFeatureFlag } from 'composable/whitelabel'
import { useRoute, useRouter } from 'vue-router'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Index from './index.vue'

export default defineComponent({
  async setup() {
    const { genSuspense } = useSuspense()
    const { features } = useFeatureFlag()
    const refComponent = ref(null)
    const route = useRoute()
    const router = useRouter()

    const saveChanges = () => {
      refComponent.value.update(() => {
        router.push({
          name: 'integration',
          query: {
            enable: route.query?.id || ''
          }
        })
      })
    }
    const genSaveButton = () =>
      h(
        'div',
        {
          class: 'flex'
        },
        [
          ...(features.value.integration.facebookPixel.learnMoreButton ? [h(
            'a',
            {
              href: 'https://help.reusely.com/en/articles/5882524-facebook-pixel',
              target: '_blank',
              rel: 'noreferrer noopener',
              class: 'my-auto'
            },
            'Learn more'
          )] : []),
          h(
            Button,
            {
              color: 'teal',
              class: 'ml-auto',
              loading:
                refComponent.value && refComponent.value?.loading?.update,
              onClick: saveChanges
            },
            {
              default: () => 'Save'
            }
          )
        ]
      )
    const genComponent = () =>
      genSuspense(
        h(Index, {
          ref: refComponent
        })
      )
    const modalProps = reactive({
      modelValue: true,
      closeTo: {
        name: 'integration',
        query: {
          reload: 1            
        }
      },
      size: 'xs'
    })
    const modalSlots = reactive({
      header: () => 'Facebook Pixel Config',
      default: () => genComponent(),
      footer: () => genSaveButton()
    })

    return () => h(Modal, modalProps, modalSlots)
  }
})
